import React, { Component } from 'react';
import axios from 'axios';
import Main from "./main";
import ReactS3 from 'react-s3';
import Question from './question';
import Audio from './audio';
import Images from './images';
import Modal from "./modal";
import Button from "./button";
import BaseQuestion from "./base-question"

export default class ListeningA extends Component {

constructor(props) {
    super(props);
    
    this.state = {
        name: '',
        order: 0,
        sentence_type: '',
        sentence: '',
        choices: [], 
        values: '',

        show: false,                
        timer: 45,
        level: 'A1',
        ptmodel: 'Academic',
        modelname: 'Listening-A',
        grammar: 'To be present tense',
        
        url: 'https://placementest-homolog.herokuapp.com/',        
        audio: '',
        files: [],
        urlFile: 'https://placementest.s3.amazonaws.com/la/',
        config: {
            bucketName: 'placementest',
            dirName: 'la', 
            region: 'us-east-1',
            accessKeyId: 'AKIAJHDYH3PMR4DKMFFA',
            secretAccessKey: '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'
        }    
    }
}

addFiles = (e) => {
    let files = this.state.files

    for(var i=0; i < e.target.files.length; i++ ) {
       if(files.filter(f => f.name === e.target.files[i].name).length === 0){
            files.push({ blob: URL.createObjectURL(e.target.files[i]), 
                            name: e.target.files[i].name,
                            file: e.target.files[i]})
       }
    }

    this.setState({ files: files})
}

addAudio = (e) => {
    var blob = URL.createObjectURL(e.target.files[0])
    var audio = {file: e.target.files[0], name: e.target.files[0].name, blob: blob}

    this.setState({audio: audio})
}

deleteFile = (e) => {
    let newlist = this.state.files.filter(f => f.name != e)
    this.setState({ files: newlist})
}

componentDidMount() {
    if(this.state.edit != undefined) {

        axios.get(this.state.url + 'questions/' +this.props.match.params.id)
            .then(response => {   
            
                let files = response.data.choices[0]
                let newfiles = []

                for(var i=0; i < files.optionlist.length; i++ ) {
                    var img = this.state.urlFile + files.optionlist[i]
                    newfiles.push({ blob: img, 
                                    name: files.optionlist[i],
                                    file: null})}

                var audio = {blob: this.state.urlFile + response.data.sentence,
                             name: response.data.sentence,
                             file: null}
                                                                    
                this.setState({
                    ptmodel: response.data.ptmodel,
                    name: response.data.name,
                    order: response.data.order,
                    modelname: response.data.modelname,
                    level: response.data.level,
                    timer: response.data.timer,
                    sentence_type: response.data.sentence_type,
                    sentence: response.data.sentence,
                    choices: response.data.choices,
                    grammar: response.data.grammar,
                    files: newfiles,
                    audio: audio,
                    values: response.data.choices[0].word
                })
            })
            .catch(function (error) {console.log(error);})
        }
}

onSubmit = (e) => {
    e.preventDefault();

    //save audio sentence AWS3
    ReactS3.uploadFile(this.state.audio.file, this.state.config)
        .then( (data) => { console.log(data) })
        .catch( (err) => { console.log(err) })  

    //save files AWS3
    let optionlist = []
    for (let i = 0; i < this.state.files.length; i++) {
        ReactS3.uploadFile(this.state.files[i].file, this.state.config)
            .then( (data) => { console.log(data) })
            .catch( (err) => { console.log(err) })  
        optionlist.push(this.state.files[i].name);
    }
    let choices = [{ word: this.state.values, optionlist: optionlist}]    
    
    const question = {
        ptmodel: this.state.ptmodel,
        name: this.state.name,
        order: this.state.order,
        modelname: this.state.modelname,
        level: this.state.level,
        timer: this.state.timer,
        sentence_type: this.state.sentence_type,
        sentence: this.state.audio.name,
        choices: choices,
        grammar: this.state.grammar,
        chuncks: ''
    };
    
    if(this.props.match.params.id == undefined) {
        axios.post(this.state.url + 'questions/add', question)
                .then(res => console.log(res.data))
                .catch((error) => {console.log(error)})
        }    else {
        axios.post(this.state.url + 'questions/update/'+this.props.match.params.id, question)
                .then(res => console.log(res.data))
                .catch((error) => {console.log(error);})
    }

    this.setState({ show: true });
}

onChange = (e) => {
    e.preventDefault();
    this.setState({ [e.target.name]:e.target.value});
}

onChangeValues = (e) => {
    this.setState({values: e})
}

hideModal = () => {
    this.setState({ show: false });
}

render() { 
    this.state.edit = this.props.match.params.id;
        return ( 
            <div> 
                <Main />
                    <BaseQuestion name="Listening A" color="card badge-pink">
                        <form onSubmit={this.onSubmit}>
                            <Question ptmodel={this.state.ptmodel} name = {this.state.name} order = {this.state.order} 
                            level = {this.state.level} timer = {this.state.timer} modelname = {this.state.modelname} 
                            grammar = {this.state.grammar} onChange={this.onChange}/>
                            <Audio audio={this.state.audio} addAudio={this.addAudio} />              
                            <Images files={this.state.files} addFiles={this.addFiles} deleteFile={this.deleteFile} 
                            values={this.state.values} onChangeValues={this.onChangeValues} />
                            <Button class="badge-pink" redirect="../../admin/question" />                
                            <Modal show={this.state.show} hideModal={this.hideModal} text ="Item added successfully!" redirect="../../admin/question"></Modal>                
                        </form>
                    </BaseQuestion>        
            </div> 
    )
  }
}