import React, { Component } from "react";
import axios from "axios";
import Main from "./main";
import Header from "./header";
import { ExportReactCSV } from "./exportcsv";

export default class Results extends Component {
  constructor(props) {
    super(props)

    this.delete = this.delete.bind(this)

    this.state = {
         url: "https://placementest-homolog.herokuapp.com/",
         listobj: [],
         grid: [],
         ptmodels: [],
         levels: [],
         modelsname: [],
         schools: [],
         students: [],
         results: [],
         student: [],
         report: [],
         dados: []
    }
  }

    onChange = (e) => {
        e.preventDefault();
        this.setState({ [e.target.name]: e.target.value });
    }

    componentDidMount() {
      axios.get(this.state.url + "results")
          .then((response) => { 
              this.setState({ dados: response.data });
          })
          .catch((error) => { console.log(error);});

          
    }

    delete = (id) => {
        axios.delete(this.state.url + "results/" + id).then((res) => console.log(res.data));
        this.setState({ grid: this.state.grid.filter((el) => el._id !== id) });
        this.setState({listobj: this.state.listobj.filter((el) => el._id !== id)});
    }

    migration = (testID, userid) => {
        this.setUser(userid) //sets the user and school to register
        this.calculateResults(testID) //calculate results 
    }

    setUser = (userid) => {
        this.setState({ userid: userid});  

        axios.get(this.state.url + 'students/' + userid)
            .then(response => {                       
                    if(response.data !== null) {              
                        this.setState({
                          student: response.data,
                          school: {id: response.data.schoolID, name: response.data.school}
                        })
                    }
                })
              .catch(function (error) {console.log(error);})

        axios.get(this.state.url + 'coaches/' + userid)
            .then(response => {         
                    if(response.data !== null) {              
                      this.setState({
                          student: response.data,
                          school: {id: response.data.schoolID, name: response.data.school}
                      })
                    }
                })
            .catch(function (error) {console.log(error);})
    }

    calculateResults = (testID) => {
        let grade = []
        let total = 0
        var levelList = [{ level: "A1", grade: 1 },{ level: "A2", grade: 2 },{ level: "B1", grade: 3 },{ level: "B2", grade: 4 },{ level: "C1", grade: 5 },{ level: "C2", grade: 6 }]
        let resultList = [] 

        axios.get(this.state.url + "results/" + testID)
            .then((response) => {
                
                response.data.map((m) => {
                    resultList = response.data
                    var level = m.questions[m.questions.length - 1].level    
                                        
                    if(m.results) {
                        console.log("ja existe, esta ok!")
                    } else {
                        console.log("vamos criar...")
                        var finalresult = {mistakes: 0, mistakes_count: 0, correct: 0, correct_count: 0, count: 0}
                        m.questions.map(question => {      
                            if(question.iscorrect === false) {
                                finalresult.mistakes = finalresult.mistakes + question.wrong
                                finalresult.mistakes_count = finalresult.mistakes_count + 1
                            }
                            else{
                                finalresult.correct = finalresult.correct + question.correct 
                                finalresult.correct_count = finalresult.correct_count + 1
                            }
                            finalresult.count = finalresult.count + 1
                        })
                        m.results = finalresult                        
                    }

                    console.log(m.results)

                    var finalGrade = {
                        testType: m.testType,
                        level: level,
                        grade: levelList.filter((f) => f.level === level)[0].grade,
                        detail_results: m.results,                        
                        questions: m.questions,
                        createdAt: m.createdAt
                    }

                    finalGrade.average_weight =  Math.round(m.results.correct == 0 ? 0 : m.results.correct / finalGrade.grade)
                    level = level + " = " + finalGrade.average_weight + " %"      
                    grade = grade.concat(finalGrade)
                    total = total + finalGrade.grade                    
                })
        
                var finalgrade = Math.round(total / (grade.length + 1))
                finalgrade = levelList.filter((f) => f.grade === finalgrade)[0].level
                this.setState({ finalGrade: finalgrade })
      
                this.createReport(grade, finalgrade, testID)
          })
          .catch((error) => { console.log(error);})
    }

    createReport = (gradebyability, finalgrade, testID) => {
        const report = {
            user: this.state.student.name,
            userID: this.state.userid,
            school: this.state.school.name, //change for school (check if the drop will filter)
            schoolID: this.state.school.id,
            testID: testID,
            testByAbility: gradebyability,
            finalGrade: finalgrade,
            date: gradebyability[0].createdAt
        }
    
        console.log(report)
    
        axios.post(this.state.url + "report/add", report)
             .then((res) => {
                 console.log(res.data);
             })
             .catch((error) => { console.log(error);});
    }

  render() {
    return (
      <div>
        <Main />
        <div className="content-page">
          <div className="content">
            <div className="container-fluid">
              <div className="row">
                <div className="col-xl-9">
                  <Header class="card badge-conexia" text="MIgration" />
                  <div className="card">
                    <div className="card-body">
                      <div className="row">
                        <div className="col-sm">
                          <div className="form-group m-t-15 m-r-15 float-right">
                            <a className="btn btn-secondary waves-effect waves-light" href="#" onClick={() => { this.filterGrid();}}>
                              Search
                            </a>
                          </div>
                        </div>
                      </div>
                      <div className="col-md-4 center">
                          <ExportReactCSV csvData={this.state.dados} fileName="larissa.csv"/>
                      </div>      
                      <div className="table-reponsive">
                        <div className="col">
                          <table className="table table-hover mb-0">
                            <thead>
                              <tr>
                                <th>UserID</th>
                                <th>Test ID</th>
                                <th>Test Ty</th>
                                <th>CreatedAt</th>
                                <th>Question</th>
                                <th>Del</th>
                                <th>Mig</th>
                              </tr>
                            </thead>
                            <tbody>
                              {this.state.dados.map((item, key) => (
                                <tr>
                                  <td>{item.userid}</td>
                                  <td>{item.testID}</td>
                                  <td>{item.testType}</td>
                                  <td>{item.createdAt}</td>
                                  <td>                                     
                                  </td>
                                  <td>
                                      <a className="btn btn-sm btnred" href="#" onClick={() => { this.delete(item._id);}}>Delete</a>
                                  </td>
                                  <td> 
                                      <a className="btn btn-sm" href="#" onClick={() => { this.migration(item.testID, item.userid);}}>Migrate</a>
                                  </td>
                                </tr>
                              ))}
                            </tbody>
                          </table>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
