export const aquecimento = (path) => {
    var next_model = parseInt(path.split("/")[2]) + 1
        
    if(next_model > 10) {
        window.location = "../start"
    }
    else if (next_model === 8) {
        window.location = "../teste-dispositivo"
    } else if(!(next_model)) {
        window.location = "../aquecimento/9"
    }
    else
        window.location = "../aquecimento/" + next_model
}

