export const apiURL = (type) => {
    switch (type) {
        case "PROD":
            return "https://placementest-homolog.herokuapp.com/"
        case "DEV":
            return "http://localhost:3000/"
        default:
            return "https://placementest-homolog.herokuapp.com/"
    }
}

