import React, { Component } from "react";
import axios from "axios";
import Header from "./header";
import moment from "moment";
import { schoolAction } from '../scripts/school'
import { classroomAction } from '../scripts/classroom'

export default class Dashboard extends Component {
    constructor(props) {
        super(props);
        this.state = {
            url: "https://placementest-homolog.herokuapp.com/",
            levels: ["All", "A1", "A2", "B1", "B2", "C1", "C2"],
            schools: [],
            students: [],
            classrooms:[],
            school: "All",
            student: "All",
            level: "All",
            classroom: "All",
            grid: [],
            export: [],
            //report: [],
        }
    }

    async componentDidMount() {
        const school = await schoolAction("GET")
        this.setState({ schools: school })

        //it filters classrooms and students together
        const classroom = await classroomAction("GET")
        this.setState({ classrooms:classroom })
        
        var listStudent = []
        classroom.filter(f => {
            for(var i=0; i<f.users.length; i++) {
                if((f.users[i].profile === "STUDENT")){ listStudent = listStudent.concat(f.users[i])}
            }
        })
        this.setState({ students: listStudent })

        const report = await axios.get(this.state.url + "report")
      
        var listReport = []
        classroom.map(m => {
            var reports = report.data.filter(r => r.classroom._id === m._id)
            listReport = listReport.concat(reports)

        })

        this.setState({ grid: listReport, dataset: listReport }) 

    }

    onChange = (e) => {
        e.preventDefault();
        this.setState({ [e.target.name]: e.target.value });
    }

  render() {
    return (
          <div>               
                {this.state.grid.map((item, key)=>(
                  <>
                    <div className="row">
                        <div className="col-sm"> {moment(item.date).format("l")} </div>
                        
                        <div className="col-sm"> Student: {item.user.name}  <br/>
                                                 School: {item.school.name} <br/>
                                                 Classroom: {item.classroom.name} 
                        </div>
                        <div className="col-sm"> Final Grade: {item.finalGrade} </div>
                        

                        <div className="col-sm"> Listening: {item.listening.level} - {item.listening.weight} % <br/>
                        Grammar: {item.grammar.level} - {item.grammar.weight} % <br/>
                        Reading: {item.reading.level} - {item.reading.weight} % <br/>
                        Writing: {item.writing.level} - {item.writing.weight} % <br/>
                        Speaking: {item.speaking.level} - {item.speaking.weight} % <br/>
                        </div>
                      
                        
                      
                    </div>
                    <div className="linha"></div>
                  
                  </>
              ))}
          </div>
                   
    );
  }
}
