import React from "react";
import { CSVLink } from "react-csv";     
// import Button from 'react-bootstrap/Button';

export const ExportReactCSV = ({ csvData, fileName }) => {
  return (
    <div>
      <CSVLink
        className="btn btn-warning waves-effect waves-light"
        data={csvData}
        filename={fileName}
      >
        Export
      </CSVLink>
    </div>
  );
};
